import React from 'react';

const Person = ({ person, filter, handleButtonClick }) =>
{
    var slice = person.name.slice(0, filter.length)
    var lowerslice = slice.toLowerCase()
    var lowerfilter = filter.toLowerCase()
    if (lowerfilter === lowerslice || filter === '') {
      return (
        <div>
          {person.name} {person.number}
          <button onClick={handleButtonClick} id={person.id}>
            Delete
          </button>
        </div>
      )
	}
    return null
}

export default Person