import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Person from './components/Person'
import personService from './services/person'
import Notification from './components/Notification'

const App = () => {
  const [persons, setPersons] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newFilter, setNewFilter ] = useState('')
  const [ errorMessage, setErrorMessage ] = useState(null)
  const [ style, setStyle ] = useState(null)

  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
      })
  }, [])

  const addPerson = (event) => 
  {
    event.preventDefault()
    const personObject = {
      name: newName,
      number: newNumber
	}
    var names = persons.map((person) => person.name)
    const id = names.indexOf(personObject.name)
    if (id !== -1) 
    {
      if (window.confirm(`${personObject.name} is already added to phonebook, replace the old number with a new one?`)) 
      {
        const person = persons.find(n => n.id === id + 1)
        console.log({person})
        const changePerson = { ...person, number: newNumber}
        personService
          .update(id + 1, changePerson).then(returnedPerson => {
            setPersons(persons.map(person => person.id !== id + 1 ? person : returnedPerson))    
          })
          .catch(error => {
            setErrorMessage(
              `Person ${personObject.name} was already deleted from server` 
			      )
            setStyle("error")
            setTimeout(() => {
              setErrorMessage(null) 
			      }, 4000)
            setPersons(persons.filter(n => n.id !== id + 1))
					})
        setNewName('')
        setNewNumber('')
        setErrorMessage(
          `${personObject.name} number changed` 
			  )
        setStyle("success")
        setTimeout(() => {
          setErrorMessage(null) 
			  }, 2000)
		  }
	  }
    else {
      setPersons(persons.concat(personObject))
      personService
        .create(personObject)
          .then(returnedPerson => {
            setPersons(persons.concat(returnedPerson))
            setNewName('')
            setNewNumber('')
				  })
      setErrorMessage(
        `Added ${personObject.name}` 
			)
      setStyle("success")
      setTimeout(() => {
        setErrorMessage(null) 
			}, 2000)
    }
  }
  
  const handlePersonChange = (event) => 
  {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => 
  {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => 
  {
    setNewFilter(event.target.value)
  }
  
  const handleButtonClick = (event) =>
  {
    const targetId = event.target.id - 0
    const person = persons.find(n => n.id === targetId)
    if (window.confirm(`Delete ${person.name}?`)) 
    {
      personService
        .deletePerson(person.id)
        .catch(error => {
          setErrorMessage(
            `Person ${person.name} was already deleted from server` 
			    )
          setStyle("error")
          setTimeout(() => {
            setErrorMessage(null) 
			    }, 4000)
				})
      setPersons(persons.filter(person1 => person1.id !== person.id))
      setErrorMessage(
        `${person.name} removed` 
			)
      setStyle("success")
      setTimeout(() => {
        setErrorMessage(null) 
			}, 2000)
		}
	}

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={errorMessage} style={style} />
      <Filter text='filter shown with' newFilter={newFilter} handleFilterChange={handleFilterChange} /> 
      <h2>Add a new</h2>
      <PersonForm addPerson={addPerson} newName={newName} handlePersonChange={handlePersonChange} newNumber={newNumber} handleNumberChange={handleNumberChange} />
      <h2>Numbers</h2>
      {persons.map(person =>
        <Person key={person.name} person={person} filter={newFilter} handleButtonClick={handleButtonClick} />
      )}
    </div>
  )

}

export default App